.search-panel__container {
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.search-input {
    width: 100%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.filters{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 550px;
}

.checkbox-label {
    font-size: 16px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: end;
}

.checkbox-label input {
    margin-right: 5px;
}
